import { Injectable, OnInit, OnDestroy } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { Subscription } from 'rxjs';
import { filter, map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class AnimationTriggerService {
  private numberOfRoutes;

  constructor(private router: Router) {
    this.numberOfRoutes = 0;
    this.router.events
      .pipe(filter(data => data instanceof NavigationEnd))
      .subscribe((data: NavigationEnd) => (this.numberOfRoutes = data.id));
  }

  triggerAnimation(): boolean {
    return this.numberOfRoutes > 0;
  }
}
