import {
  Component,
  OnInit,
  Input,
  Inject,
  PLATFORM_ID,
  OnDestroy,
} from '@angular/core';
import { isPlatformBrowser } from '@angular/common';
import {
  heroAnimations,
  heroTravelAnimation,
  heroLoadingAnimation,
} from '../../app.animations';
import { Router, NavigationEnd } from '@angular/router';
import { Subscription } from 'rxjs';
import { AnimationTriggerService } from '../../services/animation-trigger.service';

export interface Hero {
  image?: string;
  top?: string;
  header?: string;
  p?: string;
  ptemp?: string;
  linkOne?: Link;
  linkTwo?: Link;
  version?: string; // light, map
  size?: string; // small medium full
  disableScrollDown?: boolean;
}

interface Link {
  text: string;
  href: string;
  type: string; // internal, anchor, external
}

@Component({
  selector: 'app-hero',
  templateUrl: './hero.component.html',
  styleUrls: ['./hero.component.scss'],
  animations: [heroAnimations, heroTravelAnimation, heroLoadingAnimation],
})
export class HeroComponent implements OnInit, OnDestroy {
  @Input() hero: Hero;
  triggerAnimation: boolean;
  triggerTravelAnimation: boolean;
  loading: boolean;
  private routerSubscription: Subscription;

  constructor(
    @Inject(PLATFORM_ID) private platformId: any,
    private trigger: AnimationTriggerService
  ) {}

  ngOnInit() {
    this.loading = false;
    this.triggerAnimation = false;
    this.triggerTravelAnimation = false;

    if (isPlatformBrowser(this.platformId)) {
      this.loading = this.trigger.triggerAnimation();
      this.triggerAnimation = this.trigger.triggerAnimation();
      const im = new Image();
      im.src = this.hero.image;
      im.onload = () => {
        this.triggerTravelAnimation = true;
        // Check if this is the first page
      };
    } else {
      // Do not start animations on server,
      this.loading = false;
      this.triggerAnimation = false;
      this.triggerTravelAnimation = false;
    }
  }

  scrollTo(item: string) {
    if (isPlatformBrowser(this.platformId)) {
      const id = document.querySelector(item);
      if (id) {
        id.scrollIntoView({ block: 'start', behavior: 'smooth' });
      }
    }
  }

  ngOnDestroy() {
    // this.routerSubscription.unsubscribe();
  }
}
